<template>
  <el-steps align-center :active="step"  style="width: 90%;margin: 0 auto;">
    <el-step v-for="item in steps" :icon="item.icon" :key="item.id" :title="item.title" @click.native="skipStep(item.id)"></el-step>
  </el-steps>
</template>

<script>
export default {
  name: 'RecommendStep',
  props: {
    step: {
      type: Number,
      default: 0
    },
    steps: {
      type: Array,
      required: true
    }
  },
  methods: {
    skipStep (id) {
      const stepObj = this.steps.find(item => item.id === id)
      this.$router.push(stepObj.path)
    }
  }
}
</script>

<style scoped>

</style>



